import { Component, OnInit } from '@angular/core';
import { TokenService } from '../../services/token.service';
import { HttpClient } from '@angular/common/http';
import { ApiService } from '../../services/api.service';
import { SnotifyService } from 'ng-snotify';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  public form = {
    email : null,
    access_token : null
  };

  public notice = null;

  user = null;
  public role = null;

  constructor(private token : TokenService, private http : HttpClient, private api : ApiService, private notify : SnotifyService) { }

  ngOnInit() {
    this.user = JSON.parse(this.token.getUser());
    this.role = this.user.role;
    this.form.email = this.user.email;
    this.form.access_token = this.token.get();

    return this.api.post('notice/get', this.form).subscribe(
      data => this.handler(data),
      error => this.notify.error(error.error.error, {timeout:0})
    );
  }

  handler(data){
    //console.log(JSON.parse(data));
    //this.notice = JSON.parse(data);
    this.notice = data;
  }

}
